@import url(http://fonts.googleapis.com/css?family=Shadows+Into+Light);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

div {
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  /* background-color: #222; */
  height: 150px;
  padding: 20px;
  color: black;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.sidebar {
  background-color: #9e9e9e3b;
  border-radius: 1px solid black;
  position: absolute;
  display: flex;
  flex-direction: column;
  padding: 2em;
  z-index: 2;
}

.placeholder {
  height: 3em;
}

.menu {
  position: absolute;
  top: 1em;
  left: 1em;
  width: 30px;
  height: 30px;
  transition: all 300ms cubic-bezier(.61, .01, .42, 1);
  cursor: pointer;
  background: transparent;
  border: 0px;
  z-index: 3;
}


.menu div{
  height: 0px;
  border: 1.5px solid black;
  width: 22px;
  display: block;
  position: absolute;
  transition: all 300ms cubic-bezier(.61, .01, .42, 1);
  background:#fff;
  transition: 400ms;
}

.menu div:nth-child(1) {
  top: 0px;
}

.menu div:nth-child(2) {
  top: 8px;
}
.menu div:nth-child(3) {
  top: 16px;
}

.sidebar:hover {

}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.projects-container {
  display: flex;
  height: 100%;
  justify-content: center;
}

#home {
  background-color: #fbfafa;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

#davidGif {
  height: 250px;
  width: 250px;
  align-self: center;
}


.project {
  width: 300px;
}

body,
html,
div.board,
div#react-container {
    height: 100%;
    overflow: hidden;
}

body {
    margin: 0;
    padding: 0;
}

div.board {
    background-color: brown;
    width: 100%;
    height: 1000px;
    background: #eab92d;
    background: radial-gradient(ellipse at center, #eab92d 57%, #c79810 99%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#eab92d', endColorstr='#c79810', GradientType=1);

}

div.note {
    height: 150px;
    width: 150px;
    background-color: yellow;
    margin: 2px 0;
    position: absolute;
    cursor: -webkit-grab;
    box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, .2);
}

div.note:active {
    cursor: -webkit-grabbing;
}

div.note p {
    font-size: 22px;
    padding: 5px;
    font-family: "Shadows Into Light", Arial;
}

div.note:hover > span {
    opacity: 1;
}

div.note > span {
    position: absolute;
    bottom: 2px;
    right: 2px;
    opacity: 0;
    transition: opacity .25s linear;
}

div.note button {
    margin: 2px;
}

div.note > textarea {
    height: 75%;
    background: rgba(255, 255, 255, .5);
}

div.board button {
  color: #fff;
  font-size: 14px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.55);
}

#add {
  background-color: #5cb85c;
  height: 2.5em;
  position: relative;
  top: 50px;
}

#edit {
  background-color: #337ab7;
}

#save {
  background-color: #5cb85c;
}

#remove {
  background-color: #d9534f;
}

